class EditableCell extends React.Component {

  getInput = () => {
    if (this.props.dataIndex === "Attended Seller") {
      return <Select style={{ width: 150 }}>
        <Option value="Yes">Yes</Option>
        <Option value="No">No</Option>
      </Select>
    }
    if (this.props.dataIndex === "Enforcement Status") {
      return <Select style={{ width: 140 }}>
        <Option value="00 Open">00 Open</Option>
        <Option value="01 Warning Letter">01 Warning Letter</Option>
        <Option value="02 Notice Letter">02 Notice Letter</Option>
        <Option value="03 Legal">03 Legal</Option>
        <Option value="04 Closed">04 Closed</Option>
      </Select>
    }
    if (this.props.dataIndex === "date" || this.props.dataIndex === "Enforcement Date") {
      return <DatePicker id={this.props.dataIndex}
        defaultValue={(moment())} format={dateFormat} onChange={this.Change} />
    }
    return <Input allowClear style={{ width: 250 }} autoComplete="off" />;
  };

  renderCell = ({ getFieldDecorator }) => {
    const {
      editing,
      dataIndex,
      title,
      inputType,
      record,
      index,
      children,
      ...restProps
    } = this.props;
    return (
      <td {...restProps}>
        {editing ? (
          <Form.Item style={{ margin: 0 }}>
            {getFieldDecorator(dataIndex, {
              rules: [
                {
                  required: false,
                  message: `Please Input ${title}!`
                }
              ],
              initialValue: (dataIndex === "date" || dataIndex === "Enforcement Date") ? (moment(record[dataIndex], dateFormat)) : record[dataIndex],
            })(this.getInput())}
          </Form.Item>
        ) : (
            children
          )}
      </td>
    );
  };

  render() {
    return (
      <EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
    );
  }
}

