  componentDidMount() {
    dataPacket = [];
    return new Promise((resolve, reject) => {
      axios.get(process.env.REACT_APP_DOMAIN + '/api/v1/sdn/getAllSdnData')
        // axios.get('http://scienaptic.mockable.io/sdn')
        .then(response => {
          response.data.forEach((data) => {
            dataPacket.push({
              key: 'sdn' + data.id,
              "Country Marketplace": data.countryMarketPlace,
              "Country Seller": data.countrySeller,
              "Seller Name": data.sellerName,
              "Partner ID": data.partnerId,
              "Attended Seller": data.attendedSeller,
              "Type of Partner": data.typeOfPartner,
              "Type of SKU": data.typeOfSku,
              "SKU Number": data.skuNumber,
              "url": data.url,
              "date": data.date,
              "SDC Scoring": data.sdcScoring,
              "SDC Violations": data.sdcViolations,
              "Enforcement Status": data.enforcementsStatus,
              "Enforcement Date": data.enforcementsDate,
              "notes": data.notes,
              "outcome": data.outcome,
              "Partner Name": data.partnerName,
              "Partner ID" : data.partnerId
            })
          })
          this.setState({
            data: dataPacket,
            backupData: dataPacket,
            downloadData: dataPacket,
            load: false
          }, () => this.getRegions())
        })
        .catch(error => {
          this.setState({
            dataRecevied: true
          })
          window.location.hash = "/";
        })
    })
  }

