import React from 'react';
import 'antd/dist/antd.css';
import { DatePicker } from 'antd';
import moment from 'moment';

const { RangePicker } = DatePicker;

const dateFormat = 'MM/DD/YYYY';

class DateRange extends React.Component{
    onChange = (date,dateString) =>{
        this.props.action(date,dateString,this.props.id);
    }
    render(){
        let template = [];
        if(this.props.value.length === 0){
            template.push(<RangePicker
                format={dateFormat} onChange={this.onChange} style={{width:this.props.width}}
                value={[this.props.value.length !== 0 ? moment(this.props.value[0]) : moment().subtract(7, 'days'), this.props.value.length !== 0 ? moment(this.props.value[1]):moment()]}
            />)
        }
        else{
            template.push(<RangePicker
                format={dateFormat} onChange={this.onChange} style={{width:this.props.width}}
                // value={[this.props.value[0] !== '' ? moment(this.props.value[0]) : null, this.props.value[1] !== '' ? moment(this.props.value[1]):null]}
                value={[this.props.value[0] !== '' ? moment(this.props.value[0]) : null, this.props.value[1] !== '' ? moment(this.props.value[1]):null]}
            />)
        }

            return(
                <div>
                    {template}
                </div>
            )
    }
}

export default DateRange;